import React from 'react';
import JsxParser from 'react-jsx-parser';
import BoldPar from './BoldPar';
import ReactHtmlParser from 'react-html-parser';
import ArticleParagraph from './ArticleParagraph';
import ArticleSubheading from './ArticleSubheading';

const AfiliateItemSection =({itemTitle, itemLink, imageHTML, details,recommend, price}) =>{
    let priceMessage = "Price: $"+price+" USD";
   return(
   <>
    <a href={itemLink}>
        <ArticleSubheading subHeadingContent={itemTitle}/>
    </a>
    <div className="amz_img_container">
        {ReactHtmlParser(imageHTML)}
    </div>
    <BoldPar paragraphContent={"Why we recommend it: "}/>
    <ArticleParagraph keyId={0} paragraphContent={recommend}/>
    <BoldPar paragraphContent={"Details: "}/>
    <ArticleParagraph keyId={0} paragraphContent={details}/>
    <BoldPar paragraphContent={priceMessage}/>
    </>
)};

export default AfiliateItemSection;